import { observer } from "mobx-react";
import styled from "styled-components";
import { workState } from "../../store";
import { parseSecondsToTimeString } from "../../utils/time";

const ResultDetail = observer(() => {
  const result = workState.getResultDetail();
  return (
    <ComponentWrapper>
      <ResultDetailRow>
          : {parseSecondsToTimeString(result.totalWorkTime)}
      </ResultDetailRow>
      <ResultDetailRow>
          : {parseSecondsToTimeString(result.overWorkTime)}
      </ResultDetailRow>
      <ResultDetailRow>
          : {parseSecondsToTimeString(result.requiredWorkTime)}
      </ResultDetailRow>
      <ResultDetailRow>{result.frase}</ResultDetailRow>
    </ComponentWrapper>
  );
});

export default ResultDetail;

const ComponentWrapper = styled.div`
  margin: 1rem;
`;

const ResultDetailRow = styled.div``;
